// colors
@import '../../../styles/colors.scss';

// breakpoints
@import '../../../styles/breakpoints.scss';

.modalBody {
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    background: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContent {
    width: 80%;
    max-width: 80%;
    max-height: 80%;
    height: 80vh;
    background-color: $gray-1;
    padding: 10px 10px 10px 10px;
    box-sizing: border-box;

    @include sm {
        max-width: 100%;
        width: 100%;
        max-height: 100%;
        height: 100vh;
        padding-top: 10px;
    }
}