// colors
@import '../../../styles/colors.scss';

.button {
    background: none;
    border: none;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 34px;
    
    img {
        width: 16px;
    }

    &:disabled {
        opacity: 0.5;

        &:hover {
            cursor: not-allowed !important;
        }
    }

    &.full {
        width: 100%;
    }
    
    &.transparent {
        border-radius: 4px;
        &:hover {
            cursor: pointer;
            background-color: rgba(0,0,0,0.1);
        }
    }

    &.success {
        background-color: $success-1;
        border-radius: 4px;
        color: $white;
        &:hover {
            cursor: pointer;
            background-color: $success-2;
        }
    }

    &.danger {
        background-color: $danger-1;
        border-radius: 4px;
        color: $white;
        &:hover {
            cursor: pointer;
            background-color: $danger-2;
        }
    }

    &.small {
        font-size: 12px;
    }

    &.medium {
        font-size: 16px;
        padding: 8px;
    }

    &.large {
        font-size: 18px;
        padding: 8px;
    }
}