.head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    
    .heading {
        font-weight: 500;
        margin: 0;
    }

}
.productsGrid {
    height: calc(100% - 36px);
    overflow-y: auto;

    &::-webkit-scrollbar {
        display: none;
    }
}
