// colors
@import '../../../styles/colors.scss';

// breakpoints
@import '../../../styles/breakpoints.scss';

.appHeader {
    background: $dark-2;
    padding: 15px 20px;
    box-shadow: 0px 20px 15px -20px $dark-4;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    
    h4 {
        margin: 0;
        line-height: normal;
        font-weight: 500;
        color: $gray-1;
    }

    .buttons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 5px;
    }
}

.body {
    padding: 10px;
}