// breakpoints
@import '../../../styles/breakpoints.scss';

.body {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;

    @include md {
        grid-template-columns: 1fr 1fr 1fr;
    }

    @include sm {
        grid-template-columns: 1fr 1fr;
    }

    @include xs {
        grid-template-columns: 1fr;
    }
}