// colors
@import '../../../styles/colors.scss';

// breakpoints
@import '../../../styles/breakpoints.scss';

.cardBlock{
    .cardBody {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        &.dark {
            background: rgba(0,0,0,0.3);
            
            &:hover {
                cursor: pointer;
                background: rgba(0,0,0,0.5);
            }
        }

        &.light {
            background: rgba(255,255,255,0.3);
            
            &:hover {
                cursor: pointer;
                background: rgba(255,255,255,0.5);
            }
        }

        .productDataBlock{
            .imageBlock {
                padding: 8px;
                .productImage {
                    width: 100%;
                    height: 200px;
                    object-fit: cover;
                    object-position: top;
                }
            }
            
            .productTitle {
                font-size: 14px;
                margin: 0;
                padding: 0 8px 8px 8px;
                color: $white;
            }
            
            
        }
        
        .cartButtonBlock{
            padding: 8px;
            
            .productPrice {
                font-size: 18px;
                margin-top: 0;
                margin-bottom: 8px;
                font-weight: 600;
                color: $white;
            }
            .productCount {
                font-size: 18px;
                font-weight: 600;
                margin-bottom: 8px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                color: $white;
            
                .incDecButtons{
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    grid-gap: 8px;
                }
            }
        }
    }
}